export default {
	data() {
		return {
			downOption: {
				auto: false //是否在初始化后,自动执行下拉回调callback; 默认true
			},
			upOption: {
				auto: false
			},
			tab_list: [
				{
					name: '进行中的任务'
				},
				{
					name: '已完成的任务'
				}
			],
			TabCur: 0,
			detail_data: {},
			personinfo: {}
		};
	},
	components: {
		// sunuiStar
	},
	computed: {
		c_CustomBar() {
			return this.CustomBar;
		},
		personHours() {
			return this.$utils._get(this.detail_data, 'personHours', {});
		},
		hasph(){
			return JSON.stringify(this.personHours) !== '{}'
		},
		finshTask() {
			return this.$utils._get(this.detail_data, 'finshTask', []);
		},
		activeTask() {
			return this.$utils._get(this.detail_data, 'activeTask', []);
		}
	},
	onLoad(option) {
		this.personinfo = JSON.parse(decodeURIComponent(option.item));
		uni.$on('refreshList', () => {
			console.log('refreshList');
			this.init();
		});
		this.init();
	},
	onUnload() {
		uni.$off('refreshJwt');
	},
	methods: {
		init() {
			this.$http
				.get('personwx.personconsol/1.0/', {
					id: this.personinfo.proId,
					pid: this.personinfo.pid
				})
				.then(res => {
					console.log(res);
					if (res.data.code == 0) {
						this.detail_data = res.data.data;
					}
				});
		},
		downCallback(mescroll) {
			// 这里加载你想下拉刷新的数据, 比如刷新轮播数据
			// loadSwiper();
			// 下拉刷新的回调,默认重置上拉加载列表为第一页 (自动执行 mescroll.num=1, 再触发upCallback方法 )
			mescroll.resetUpScroll();
		},
		/*上拉加载的回调: mescroll携带page的参数, 其中num:当前页 从1开始, size:每页数据条数,默认10 */
		upCallback(mescroll) {
			//联网加载数据
			mescroll.endErr();
			// setTimeout(() => {
			// 	mescroll.endErr();
			// }, 1000);
		},

		avatarUrl(avatarUrl) {
			return `url(${avatarUrl})`;
		},
		back() {
			uni.navigateBack({
				delta: 1
			});
		},
		tabSelect(e) {
			this.TabCur = e.currentTarget.dataset.id;
		}
	}
};
